<template>
    <li class="border border-slate-600 rounded-md text-sm font-inter-normal p-3 select-none duration-300"
    :class="{
          'bg-slate-600 text-amber-50': isSelected,
          'text-slate-600 cursor-pointer hover:scale-105': notOneButtonSelected,
          'text-slate-600': !isSelected,
        }"
    @click="buttonClick"
    >
        <span>{{answerButtonInfo.buttonText}}</span>
    </li>
</template>

<script>
export default {
    name: "AnswerButton",
    components: {

    },
    props: {
        answerButtonInfo: {},
        answerNumber: Number,
        currentAnswerNumber: Number,
        notOneButtonSelected: Boolean,
        isDisable: Boolean
    },
    computed: {
        isSelected() {            
            if (this.answerNumber === this.currentAnswerNumber) {
                return true
            }
            else {
                return false
            }
        }
    },
    methods: {
        buttonClick(){
            if (!this.isDisable) {
                this.$emit("selectButton", this.answerNumber)
            }
        }
    },
    mounted() {
        
    }

}
</script>

