<template>
    <li 
    class="text-sm font-inter-normal select-none flex text-slate-600 w-full justify-start"    
    >
    <input
          type="checkbox"
          class="w-5 h-5 mr-4 shrink-0"
          :id = "checkboxId"
          :checked = "isChecked"
          :disabled="isDisable"
          @click = "selectCheckbox"
        >
        <label :for="checkboxId" class="w-full">
            {{answerCheckboxInfo.checkboxText}}
        </label>
    </li>
</template>

<script>
export default {
    name: 'AnswerCheckbox',
    props: {
        answerCheckboxInfo: {},
        answerNumber: Number,
        isChecked: Boolean,
        isDisable: Boolean
    },
    computed: {
        _isChecked: {
            get() {
                return this.isChecked
            },
            set() {
                this.selectCheckbox()
            }
        },

        checkboxId() {
            return ("checkbox" + this.answerNumber)            
        }
    },
    methods: {
        selectCheckbox() {
            this.$emit('selectCheckbox', this.answerNumber)
        }
        
    },
    mounted() {
        
    },
}

</script>

