<template>
  <div>
    
  <GameTitle 
    :titleText = actualView.title
  />

  <div 
  :class="classBodyContent"
  style="max-height: calc(100vh - 250px);"
  >
    <div
        v-for="(item,index) in actualView.bodyContent"
        :key="index"
    >
        <InfoText
          v-if="item.contentText"
          :contentText="item.contentText"
        >            
        </InfoText>

        <InfoImage
          v-if="item.contentImage"
          :contentImage="item.contentImage"
        >
        </InfoImage>

    </div>

    <AcceptCheckbox
      v-if="actualView.acceptCheckbox"  
      :isAccept=isAccept
      @changeAccept = changeAccept
    />

    <UserFeedback
      v-if="actualView.feedback"
    />    

  </div>

  

  <GameFooter 
    :footerData ="actualView.footer"
  />

  </div>
  </template>
  
 <script>
import { mapGetters } from 'vuex'
import InfoText from '@/components/game/InfoText.vue'
import InfoImage from '@/components/game/InfoImage.vue'
import GameFooter from '@/components/game/GameFooter.vue'
import GameTitle from '@/components/game/GameTitle.vue'
import AcceptCheckbox from '@/components/game/AcceptCheckbox.vue'
import UserFeedback from '@/components/game/UserFeedback.vue'


export default {
    name: "InfoContent",
    data() {
      return {
        isAccept: false
      }
    },
    components: {
        InfoText,
        InfoImage,
        GameFooter,
        GameTitle,
        AcceptCheckbox,
        UserFeedback
    },
    computed: {
      ...mapGetters([
          "gameData",
          "stepNumber",
          "actualView"
      ]),
      classBodyContent(){
        if (this.actualView.acceptCheckbox) {
          return "px-28 py-6 border-b border-slate-700 flex flex-col"
        }
        else {
          return "px-12 py-6 flex-col flex items-center overflow-y-auto"
        }
      }        
    },
    methods: {
      changeAccept() {
        this.isAccept = !this.isAccept
      }
    },
}
</script>

