      <span class="font-inter-normal text-md text-slate-600">
        Только {{randomPercent}}% пользователей сумели пройти этот сценарий с первого
        раза!
      </span>
      <img src="@/assets/images/cup.svg" alt="cup" width="30" />
    </div>
</template>

<script>
export default {
  name: 'PercentText',
  computed: {
    randomPercent(){
        return (Math.random() * (73 - 61) + 61).toFixed(0)
    }    
  }
}
</script>

