<template>
    <div
    v-if="contentImage.length === 2"
    class="flex justify-center gap-6 mt-5"
    >

        <div
        v-for="(item, index) in contentImage"
        :key="index"
        class="flex flex-col items-center w-[32%] relative"
        >
            
            <img
            v-if="item.image"
            :src="getImage(index)" alt="image" class="mt-5 rounded-md">

            <ImageTopLabel
            v-if="item.topLabel"
            :textTopLabel = item.topLabel
            />

            <ImageBottomLabel
            v-if="item.bottomLabel"
            :textTopLabel = item.bottomLabel
            />

        </div>    

    </div>
    <div
        v-else-if="contentImage.length === 1"
        class="px-12 py-6 flex-col flex items-center relative"
    >
        
        <img
        v-if="contentImage[0].image"
        :src="getImage(0)" alt="image" class="w-[60%] mt-5 rounded-md">

        <!-- <ImageTopLabel
        v-if="contentImage[0].topLabel"
        :textTopLabel = contentImage[0].topLabel
        /> -->

        <ImageBottomLabel
        v-if="contentImage[0].bottomLabel"
        :textTopLabel = contentImage[0].bottomLabel
        />

    </div>

</template>
  
<script>

import ImageTopLabel from '@/components/game/ImageTopLabel.vue'
import ImageBottomLabel from '@/components/game/ImageBottomLabel.vue'

export default {
    name: 'InfoImage',
    components: {
        ImageTopLabel,
        ImageBottomLabel
    },
    props: {
        contentImage: []
    },
    methods: {
        getImage(index) {
            return require(`@/assets/images/${this.contentImage[index].image}`)
        }
    }
    
}
</script>
  
