<template>
    <div>
        <GameTitle 
        :titleText = actualView.title
        />

        <div class="px-8 py-6">
            <ul class="grid grid-cols-1 gap-5 sm:grid-cols-2">
                <AnswerButton
                v-for="(item,index) in actualView.bodyButtons"
                :key="index"
                :answerButtonInfo = item
                :answerNumber = index
                :currentAnswerNumber = currentAnswerNumber
                :notOneButtonSelected = notOneButtonSelected
                :isDisable = "actualProperty.completed"
                @selectButton = "selectButton($event)"
                />
            </ul>
            
            <GameFooter 
            :footerData ="actualView.footer"
            />

        </div>
        
    </div>
</template>

<script>
import { mapGetters } from 'vuex'
import { mapActions } from 'vuex'
import GameFooter from '@/components/game/GameFooter.vue'
import GameTitle from '@/components/game/GameTitle.vue'
import AnswerButton from '@/components/game/AnswerButton.vue'

export default {
    name: "AnswersButtons",
    components: {
        GameFooter,
        GameTitle,
        AnswerButton
    },
    computed: {
      ...mapGetters([
          "actualView",
          'actualProperty',
          "stepNumber"
      ]),
      notOneButtonSelected() {
            if (this.actualProperty.currentAnswer === null) {
                return true
            }
            else {
                return false
            }
        },

        currentAnswerNumber() {
            return this.actualProperty.currentAnswer
        }
      
    },
    methods: {
        ...mapActions([
        "setCurrentAnswer"
        ]),
        selectButton(answerNumber){            
            this.setCurrentAnswer(answerNumber)
        }        
    },
}
</script>

