<template>
    <main class="main w-full h-screen max-h-screen flex justify-center py-6">
    <div class="w-full max-w-[850px] max-h-screen flex items-center">
      <div
        class="bg-amber-50 border-slate-700 border-2 rounded rounded-md shadow-md w-full"
      >
        <InfoContent
        v-if="actualView.type === 'info'"
        />

        <GameSections
        v-if="actualView.type === 'sections'"
        />

        <AnswersButtons
        v-if="actualView.type === 'answersButtons'"
        />

        <AnswersCheckboxes
        v-if="actualView.type === 'answersCheckboxes'"
        />

      </div>
    </div>
  </main>
    
</template>
  
<script>
import { mapGetters } from 'vuex'
import { mapActions } from 'vuex'
import InfoContent from '@/components/game/InfoContent.vue'
import GameSections from '@/components/game/GameSections.vue'
import AnswersButtons from '@/components/game/AnswersButtons.vue'
import AnswersCheckboxes from '@/components/game/AnswersCheckboxes.vue'
  
export default {
    name: 'GameMain',
    components: {
        InfoContent,
        GameSections,
        AnswersButtons,
        AnswersCheckboxes
    },
    computed: {
        ...mapGetters([
            'gameData',
            'stepNumber',
            'actualView'
        ])
    },
    methods: {
        ...mapActions([
            'GET_DATA_FROM_API'
        ])
    },
    created(){
        this.GET_DATA_FROM_API()
    }
}
</script>

<style>
    .main {
        background: url('@/assets/images/bg.jpg');
    }
</style>