<template>
    <div
      class="flex mt-3 items-end cursor-pointer"
      @click="changeAccept"
    >
      <input
        type="checkbox"
        class="w-5 h-5 mr-2"
        :checked="isAccept"
      />
      <span
        class="font-inter-normal text-md"
        :class="{
          'text-slate-600': isAccept,
          'text-red-600': !isAccept,
        }"
      >
        Принимаю
      </span>
    </div>
</template>

<script>
export default {
    name: 'AcceptCheckbox',
    props: {
        isAccept: Boolean
    },
    methods: {
        changeAccept(){
            this.$emit('changeAccept')
        }
    }
}
</script>

