<template>
    <div>
        <GameTitle 
        :titleText = actualView.title
        />

        <h2 class="font-inter-normal text-md text-slate-600 text-center w-full mt-5">
            {{actualView.headerText}}
        </h2>
        <div class="px-8 py-6">
            <ul class="grid grid-cols-1 gap-4 sm:grid-cols-2">
                <AnswerCheckbox 
                v-for="(item,index) in actualView.bodyCheckboxes"
                :key="index"
                :answerCheckboxInfo = item
                :isChecked = actualProperty.checkboxList[index]
                :answerNumber = index
                :isDisable = actualProperty.completed
                @selectCheckbox = selectCheckbox(index)
                />
            </ul>
        </div>

        <GameFooter 
            :footerData ="actualView.footer"
        />

    </div>
</template>

<script>
import { mapGetters } from 'vuex'
import { mapActions } from 'vuex'
import GameFooter from '@/components/game/GameFooter.vue'
import GameTitle from '@/components/game/GameTitle.vue'
import AnswerCheckbox from '@/components/game/AnswerCheckbox.vue'

export default {
    name: 'AnswersCheckboxes',
    components: {
        AnswerCheckbox,
        GameFooter,
        GameTitle
    },
    computed: {
      ...mapGetters([
          "actualView",
          'actualProperty',
          "stepNumber"
      ])      
    },
    methods: {
        ...mapActions([
        "changeAnswerCheckbox"
        ]),
        selectCheckbox(index){
            this.changeAnswerCheckbox(index)     
        }
    }
}
</script>

